import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '@/components/Home/index'
import Login from '@/components/Login/index'

Vue.use(VueRouter)

  const routes = [
  // {
  //   path: '/',
  //   name: 'Home',
  //   component: Home
  // },
  // {
  //   path: '/about',
  //   name: 'About',
  //   // route level code-splitting
  //   // this generates a separate chunk (about.[hash].js) for this route
  //   // which is lazy-loaded when the route is visited.
  //   component: () => import(/* webpackChunkName: "about" */ '@/components/About')
  // },
  {
    path: '/',
    name: 'home',
    component: Home
  },
  {
    path: '/dashboard',
    name: 'dashboard',
    component: () => import('@/components/Dashboard/index'),
  },
  {
    path: '/login',
    name: 'login',
    component: Login,
  },
  {
    path: '/verify',
    name: 'verify',
    component: () => import('@/components/Login/Verify'),
  },
  {
    path: '/inquire',
    name: 'inquire',
    component: () => import('@/components/Inquire/index'),
  },
  {
    path: '/rate',
    name: 'rate',
    component: () => import('@/components/Rate/index'),
  },
  {
    path: '/input',
    name: 'input',
    component: () => import('@/components/Input/index'),
    children: [
      {
        path: 'inputform',
        name: 'inputform',
        component: () => import('@/components/Input/InputForm'),
      },
    ]
  },
  {
    path: '/importdata',
    name: 'importdata',
    component: () => import('@/components/ImportData/index'),
  },
  {
    path: '/record',
    name: 'record',
    component: () => import('@/components/Record/index'),
    children: [
      {
        path: 'done',
        name: 'record-done',
        component: () => import('@/components/Record/Done'),
      },
      {
        path: 'undo',
        name: 'record-undo',
        component: () => import('@/components/Record/Undo'),
      },
      {
        path: 'unsubmit',
        name: 'record-unsubmit',
        component: () => import('@/components/Record/Unsubmit'),
      },
    ]
  },
  {
    path: '/feedback',
    name: 'feedback',
    component: () => import('@/components/Feedback/index'),
  },
]

const router = new VueRouter({
  mode: 'history',
  routes
})

export default router
